.expandingWrapper {
  height: 100vh; /* needed for scroll effect */
  position: relative;
  background: transparent;
  z-index: 5;
}

.innerBox {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%) scale(0.3);
  width: 60vw;
  height: 60vh;
  background: #111;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  z-index: 10;
  will-change: transform, border-radius;
}
